html {
    font-size: 16px;
}
html,
body {
    height: 100%;
    -ms-overflow-style: none;
    /* scrollbar-width: none; */
}
/* body::-webkit-scrollbar {
  display: none;
} */
body {
    margin: 0;
    padding: 0;
    letter-spacing: -0.05em;
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

* {
    font-family: 'Noto Sans KR', sans-serif !important;
    word-break: keep-all;
}
p {
    line-height: 1.3;
}

